export default {
	data() {
		return {
			scrollHeight:'500px',
			stationArray:[],
			leftArray:[],
			mainArray:[],
			leftIndex:0,
			keywordList: [],
			isShowAllList:true,//是否显示联动列表
			isShowList:false,//是否显示站点列表
			stationType:'',//判断上个页面点击的是上车点还是下车点
		}
	},
	onLoad(param){
		var that = this;
		// console.log(param);
		that.stationType = param.station;
		//获取站点列表
		that.getBusStationList();
		/* 设置当前滚动容器的高，若非窗口的高度，请自行修改 */
		uni.getSystemInfo({
			success:(res)=>{
				this.scrollHeight=`${res.windowHeight}px`;
			}
		});
	},
	methods: {
		//-------------------------获取车站列表数据-------------------------
		getBusStationList() {
			uni.showLoading();
			uni.request({
				url:'http://27.148.155.9:9056/CTKY/getStations',
				method:'POST',
				header:{'content-type':'application/x-www-form-urlencoded'},
				data:{
					systemName:'泉运公司综合出行'
				},
				success: (res) => {
					console.log(res)
					uni.hideLoading();
					let that = this;
					// console.log(res.data);
					if (res.data.length != 0) {
						for (var i = 0; i < res.data.length; i++) {
							var cityNameArray = {
								cityName : res.data[i].cityName
							}
							this.stationArray.push(cityNameArray);
							for (var j = 0; j < res.data[i].countys.length;j++) {
								var countysArray = {
									countys : res.data[i].countys[j]
								}
								this.mainArray.push(countysArray);
							}
						}
					}
				},
				fail(res) {
					uni.hideLoading();
				}
			})
		},
		//-------------------------监听输入-------------------------
		onInput(event){
			var keyword = event.detail?event.detail.value:event;
			if (!keyword) {
				this.keywordList = [];
				this.isShowList = false;
				this.isShowAllList = true;
				return;
			}
			this.isShowList = true;
			this.isShowAllList = false;
			//以下示例截取淘宝的关键字，请替换成你的接口
			uni.showLoading();
			uni.request({
				url: 'http://27.148.155.9:9056/CTKY/getSatartSite',
				method:'POST',
				header:{'content-type':'application/x-www-form-urlencoded'},
				data:{
					systemName:'泉运公司综合出行',
					keyword:keyword
				},
				success: (res) => {
					uni.hideLoading();
					// console.log(res);
					this.keywordList = [];
					this.keywordList = this.drawCorrelativeKeyword(res.data, keyword);
				},
				fail(res) {
					uni.hideLoading();
				}
			});
		},
		//-------------------------高亮关键字-------------------------
		drawCorrelativeKeyword(keywords, keyword) {
			// console.log(keywords);
			var len = keywords.length,
				keywordArr = [];
			for (var i = 0; i < len; i++) {
				var row = keywords[i].siteName;
				//定义高亮#9f9f9f
				var html = row.replace(keyword, "<span style='color: #9f9f9f;'>" + keyword + "</span>");
				html = '<div>' + html + '</div>';
				var tmpObj = {
					keyword: row,
					htmlStr: html
				};
				keywordArr.push(tmpObj)
			}
			return keywordArr;
		},
		//-------------------------点击下拉站点-------------------------
		itemClick(index){
			var that = this;
			//获取点击选项的文字
			var key = this.keywordList[index].keyword;
			
			if (that.stationType == 'qidian') {
				//当前是上车点
				uni.$emit('startstaionChange', {
				    data: key
				});
				uni.navigateBack({ });
			}else if(that.stationType == 'zhongdian') {
				//当前是下车点
				uni.$emit('endStaionChange', {
				    data: key
				});
				uni.navigateBack({ });
			}
		},
		//-------------------------点击站点-------------------------
		detailStationTap(item){
			// console.log(item.countys);
			var that = this;
			if (that.stationType == 'qidian') {
				//当前是上车点
				uni.$emit('startstaionChange', {
				    data: item.countys
				});
				uni.navigateBack({ });
			}else if(that.stationType == 'zhongdian') {
				//当前是下车点
				uni.$emit('endStaionChange', {
				    data: item.countys
				});
				uni.navigateBack({ });
			}
			
		},
		
		//-------------------------左侧导航点击-------------------------
		leftTap(e){
			let index=e.currentTarget.dataset.index;
			this.leftIndex=Number(index);
		},
		/* 轮播图切换 */
		swiperChange(e){
			let index=e.detail.current;
			this.leftIndex=Number(index);
		}
	}
}
